import React, { Component } from 'react';
import S3FileUpload from 'react-s3'
import { Page, Toolbar, Button, BackButton, ToolbarButton, Icon, Input, Checkbox, List, ListItem, Modal } from 'react-onsenui';
import { mapKeys, values, map, isEmpty } from 'lodash'
import { config } from "./../../service";
// Webpack CSS import
import 'onsenui/css/onsenui.css';
import 'onsenui/css/onsen-css-components.css';

// Apollo imports
import { Query, Mutation } from 'react-apollo'
import gql from 'graphql-tag'
import { GET_WALLPAPER_BY_CATEGORY } from './../home'

const GET__CATEGORIES = gql`
query{
  categories{
    _id,
    title
  }
}
`
const ADD_WALLPAPER = gql`
mutation($title:String!,$url:String!,$category:[ID!],$premium:Boolean){
  addWallpaper(title:$title,url:$url,category:$category,premium:$premium){
    title
  }
}
`

class Create extends Component {
  state = {
    title:'',
    premium:false,
    category:{},
    categoryListModal:false,
    url:null,
    file:null
  }

  uploadImage = (mutation) => {
    if(this.props.file){
      S3FileUpload
      .uploadFile(this.state.file, config)
      .then( data =>{
        console.log(data)
        let category = []
        map(this.state.category,item => {
          category.push(item._id)
        })

        let modifiedData = {
          url:data.location,
          title:this.state.title,
          premium:this.state.premium,
          category
        }
        mutation({variables:modifiedData})
      })
      .catch( err => console.log(err) )
    }
    else{
      let category = []
        map(this.state.category,item => {
          category.push(item._id)
        })

      let modifiedData = {
        url:this.state.url,
        title:this.state.title,
        premium:this.state.premium,
        category
      }
      mutation({variables:modifiedData})
    }
    
    
  }
  onClickAddCategory = (row) => {
    this.setState({category:{...this.state.category,[row._id]:row}})
  }

  removeCategory = (id) => {
    let category = this.state.category
    delete category[id]
    this.setState({category})
  }

  renderCategory = (categories) => {
      return (<List
        dataSource={categories}
        renderHeader={this.renderHeader}
        renderRow={(row, idx) => (
          <ListItem
            key={row._id}
            style={{height:40}}
            onClick={()=>{
              this.onClickAddCategory(row)
            }} 
            modifier={idx === this.state.category.length - 1 ? 'longdivider' : null}>
          {row.title}
          
      </ListItem>
      )}
      />)
    
  }
  componentDidUpdate(){
  }

  onClickAddWallpaper = (mutation) => {
    if(this.state.file === null){
      if(this.state.url === null || this.state.url === '' ){
        alert('Please select the image or enter an url')
        return
      }
    }
    
    if(this.state.title === ''){
      alert("Title can't be empty")
      return
    }
    if(isEmpty(this.state.category)){
      alert('Please select at least one category')
      return
    }
    this.uploadImage(mutation)
  }

  render() {
    return (
      <Page renderToolbar={() =>
        <Toolbar>
          <div className="left">
            <BackButton onClick={()=>{
              this.props.history.goBack()
            }}>
                Back
            </BackButton>
          </div>
          <div className="center">
            Create
          </div>
        </Toolbar> }
      > 
      {/* Category List */}
      <Modal 
        onClick={()=>{
          this.setState({categoryListModal:false})
        }}
        animation="lift"
        isOpen={this.state.categoryListModal}>
        <Query query={GET__CATEGORIES}>
          {({loading,data,error})=>{
            if(loading) return <div>Loading ...</div>
            if(data){
              //let updatedCategory = mapKeys(data.categories,category=>[category._id] )
              let updatedCategory = data.categories.filter(item=>{

                if(this.state.category[item._id]) return false
                else return true
              })
              return <div>{this.renderCategory(updatedCategory)}</div>
            }
          }}
        </Query>
      </Modal>
        <div style={{padding:10}}>
          <div>
            <input type='file' id='multi' onChange={(e)=>{
              console.log(e.target.value)
              if(e.target.files[0]){
                let extention = e.target.files[0].name.split(".")
                extention = extention[extention.length - 1]
                if(extention === 'jpg' || extention === 'jpeg' || extention === 'png'){
                  this.setState({file:e.target.files[0]})
                }
                else{
                  e.preventDefault()
                  alert('Only jpg, jpeg, png is allowed')
                }
              }
              
              
            }} multiple />
          </div>
          <div>
            <h4 style={{textAlign:'center'}}>OR</h4>
          </div>
          <div style={{padding: '10px 0px',marginTop:20}}>
              <Input
                style={{width: '100%'}}
                value={this.state.url} float
                onChange={(event) => { 
                  this.setState({url: event.target.value})
                }}
                modifier='material'
                placeholder='Wallpaper url' />
            </div>
          <div>
            <div style={{padding: '10px 0px',marginTop:20}}>
              <Input
                style={{width: '100%'}}
                value={this.state.title} float
                onChange={(event) => { 
                  this.setState({title: event.target.value})
                }}
                modifier='material'
                placeholder='Wallpaper Title' />
            </div>
            <div>
              <div>
                <div>
                  <List
                    style={{display:'flex', flexWrap: 'wrap'}}
                    dataSource={values(this.state.category)}
                    renderHeader={this.renderHeader}
                    renderRow={(row, idx) => (
                      <div
                        key={row._id}
                        style={{    
                          borderRadius: 5,
                          padding: 5,
                          fontSize: 12,
                          margin:5,
                          border: '1px solid rgb(55, 146, 189)',
                          backgroundColor: '#dae7ff'
                        }}
                       >
                      {row.title}
                      <Button
                        onClick={()=>{
                          this.removeCategory(row._id)
                        }}
                        style={{
                          backgroundColor:'transparent',
                          padding:'0px 10px',
                          color:"#000"
                        }}
                      >
                      <Icon icon={{default: 'times-circle', material: 'times-circle'}}/>
                    </Button>
                  </div>
                  )}
                  />
                </div>
                <div style={{padding: '10px 0px'}}>
                  <Button 
                    onClick={()=>{
                      this.setState({categoryListModal:true})
                    }}
                    modifier="large--quiet">
                    Add Category
                  </Button>
                </div>
              </div>
              </div>
            <div>
              <Checkbox
                onChange={event => { this.setState({premium: event.target.checked})} }
                modifier='material' />
                <span> Premium </span>
            </div>
            
            <div style={{padding: '10px 0px'}}>
              <Mutation 
                mutation={ADD_WALLPAPER}
                onCompleted={(data)=>{
                  console.log("Done",data)
                  this.props.history.goBack()
                }}
                refetchQueries={[{query:GET_WALLPAPER_BY_CATEGORY}]}
                >
                {(addWallpaper,{ loading, data, error}) => {
                  if(loading) return <div>loading ...</div>
                  return (<Button 
                    onClick={()=>{
                      this.onClickAddWallpaper(addWallpaper)
                    }}
                    modifier="material">
                    Add
                  </Button>)
                }}
              </Mutation>
              
            </div>
          </div>
        </div>
      </Page>
    );
  }
}

export default Create;