import React, { Component } from 'react';
import { 
  Button, 
  Icon, 
  Card
} from 'react-onsenui';
import { withRouter } from 'react-router-dom'
// Webpack CSS import
import 'onsenui/css/onsenui.css';
import 'onsenui/css/onsen-css-components.css';
// Apollo imports
import { Mutation } from 'react-apollo'
import gql from 'graphql-tag'
import { GET_WALLPAPER_BY_CATEGORY } from './../../container/home'

import styled,{css} from 'styled-components'

const MODIFY_CATEGORY = gql`
  mutation($_id:ID!,$title:String,$subTitle:String,$parent:ID,$priority:Int){
    modifyCategory(_id:$_id,title:$title,subTitle:$subTitle,parent:$parent,priority:$priority){
      title
    }
  }
`

const DELETE_CATEGORY = gql`
  mutation($id:String!){
    removeCategory(id:$id){
      title
    }
  }
`

const Layout = styled.div`
  display:flex;
  ${props=>{
   if(props.orentation === 'Horizontal'){
    return css`
      flex-direction:row;
    `
   } 

   if(props.orentation === 'Vertical'){
    return css`
      flex-direction:column;
    `
   }
  }}
`

class CardContainer extends Component {

  onClickUp = () => {

  }

  onClickDown = () => {

  }  
  renderWallpapers = (wallpapers) => {
    return wallpapers.map(wallpaper=>{
      return (<Layout orentation='Horizontal' key={wallpaper._id} style={{margin:10}}>
        <img src={wallpaper.url} style={{width:100,borderRadius:5}}/>
      </Layout>)
    })
  }
  render() {
    return (
      <Layout orentation='Vertical' style={{padding:'0px 10px',marginBottom:20, marginTop: 10}}>
        <Mutation mutation={MODIFY_CATEGORY} refetchQueries={[{query:GET_WALLPAPER_BY_CATEGORY}]}>
          {(modifyCategory,{ loading, data, error})=>{
            return (<Layout orentation="Horizontal" style={{justifyContent:'space-between'}}>
            <Layout orentation='Vertical'>
              <div>
                {this.props.title}
              </div>
              <div style={{fontSize:12, fontStyle: 'italic'}}>
                {this.props.subTitle}
              </div>
            </Layout>
            <Layout orentation='Horizontal'>
              <Button modifier="quiet" onClick={()=>{
                modifyCategory({variables:{_id:this.props._id,priority:this.props.priority+1}})
              }}><Icon icon="angle-up"/></Button>
              <span style={{ padding: '10px 4px'}}>{this.props.priority}</span>
              <Button modifier="quiet" onClick={()=>{
                modifyCategory({variables:{_id:this.props._id,priority:this.props.priority-1}})
              }}><Icon icon="angle-down"/></Button>
              <Button modifier="quiet" onClick={()=>{
                this.props.history.push('/createCategory',{
                  _id:this.props._id,
                  title:this.props.title,
                  subTitle:this.props.subTitle,
                  priority:this.props.priority,
                  context:'edit'
                })
              }}><Icon icon="edit"/></Button>
              <Mutation mutation={DELETE_CATEGORY} refetchQueries={[{query:GET_WALLPAPER_BY_CATEGORY}]}>
                {(removeCategory,{ loading, data, error})=>{

                  return (<Button modifier="quiet" onClick={()=>{
                    removeCategory({variables:{id:this.props._id}})
                  }}><Icon icon="trash"/></Button>)
                }}</Mutation>
              
            </Layout>
          </Layout>)
          }}
        </Mutation>
        
        
        
        <Layout orentation='Horizontal' style={{overflowX:'scroll'}}>
          {this.renderWallpapers(this.props.wallpapers)}
        </Layout>
      </Layout>
      
    );
  }
}
CardContainer.defaultProps = {
  orentation:'Horizontal',
  title:'Unamed',
  subTitle:'Uname Subtitle',
  priority:1,
  _id:'',
  wallpapers:[]
  
}
export default withRouter(CardContainer);