import React, { Component, Fragment } from 'react';
import { Page, Toolbar, Button, BackButton, ToolbarButton, Icon, Input, Checkbox, List, ListItem, Modal } from 'react-onsenui';

// Webpack CSS import
import 'onsenui/css/onsenui.css';
import 'onsenui/css/onsen-css-components.css';
// Apollo imports
import { Query, Mutation } from 'react-apollo'
import gql from 'graphql-tag'
import { GET_WALLPAPER_BY_CATEGORY } from './../home'

const GET_CATEGORIES = gql`
query{
  categories{
    _id,
    title
  }
}
`

const ADD_CATEGORY = gql`
  mutation($title:String!,$subTitle:String!,$parent:ID,$priority:Int){
    addCategory(title:$title,subTitle:$subTitle,parent:$parent,priority:$priority){
      title
    }
  }
`

const MODIFY_CATEGORY = gql`
  mutation($_id:ID!,$title:String,$subTitle:String,$parent:ID,$priority:Int){
    modifyCategory(_id:$_id,title:$title,subTitle:$subTitle,parent:$parent,priority:$priority){
      title
    }
  }
`

class CreateCategory extends Component {
  state = {
    title:'',
    subTitle:'',
    parent:null,
    priority:1,
    categoryListModal:false
  }

  componentWillMount(){
    if(this.props.location.state.context === "edit"){
      this.setState({title:this.props.location.state.title,subTitle:this.props.location.state.subTitle,priority:this.props.location.state.priority})
    }
  }

  renderCategory = (categories) => {
    return (<List
      dataSource={categories}
      renderHeader={this.renderHeader}
      renderRow={(row, idx) => (
        <ListItem
          key={row._id}
          style={{height:40}}
          onClick={()=>{
            this.onClickAddCategory(row)
          }} 
          //modifier={idx === this.state.parent.length - 1 ? 'longdivider' : null}
          >
        {row.title}
        
    </ListItem>
    )}
    />)
  
  }

  onClickAddCategory = (row) => {
    this.setState({parent:row})
  }
  // call this function when add is clicked
  onClickAdd = (mutation) =>{
    if(this.state.title === ''){
      alert("Title can't be empty")
      return
    }
    if(this.state.subTitle === ''){
      alert("SubTitle can't be empty")
      return
    }
    if(this.state.priority === ''){
      alert("Priority can't be empty")
      return
    }
    let data = {
      title:this.state.title,
      subTitle:this.state.subTitle,
      priority:this.state.priority,
      parent:this.state.parent?this.state.parent._id:null
    }
    if(this.props.location.state.context === "edit")
      data._id = this.props.location.state._id
    mutation({variables:data})
  }

  
  render() {
    return (
      <Page renderToolbar={() =>
        <Toolbar>
          <div className="left">
            <BackButton onClick={()=>{
              this.props.history.goBack()
            }}>
                Back
            </BackButton>
          </div>
          <div className="center">
            {this.props.location.state.context === "create"?'Create Category':'Edit Category'}
          </div>
        </Toolbar> }
      > 
      {/* Category list */}
      <Modal 
        onClick={()=>{
          this.setState({categoryListModal:false})
        }}
        animation="lift"
        isOpen={this.state.categoryListModal}>
        <Query query={GET_CATEGORIES}>
          {({loading,data,error})=>{
            if(loading) return <div>Loading ...</div>
            if(data){
              //let updatedCategory = mapKeys(data.categories,category=>[category._id] )
              let updatedCategory = data.categories.filter(item=>{
                if(this.state.parent){
                  if(this.state.parent && this.state.parent._id === item._id) return false
                   return true
                }
                else return true
                
              })
              return <div>{this.renderCategory(updatedCategory)}</div>
            }
          }}
        </Query>
      </Modal>
        <div style={{padding:20}}>
          <div style={{padding: '10px 0px'}}>
            <Input
              style={{width: '100%'}}
              value={this.state.title} float
              onChange={(event) => { 
                this.setState({title: event.target.value})
              }}
              modifier='material'
              placeholder='Category title' />
          </div>
          <div style={{padding: '10px 0px'}}>
            <Input
              style={{width: '100%'}}
              value={this.state.subTitle} float
              onChange={(event) => { 
                this.setState({subTitle: event.target.value})
              }}
              modifier='material'
              placeholder='Category Sub title' />
          </div>
          <div style={{padding: '10px 0px'}}>
            <Input
              style={{width: '100%'}}
              type="tel"
              value={this.state.priority} float
              onChange={(event) => { 
                if(event.target.value)
                  this.setState({priority: parseInt(event.target.value)})
                else{
                  this.setState({priority: ''})
                }
              }}
              modifier='material'
              placeholder='Priority' />
          </div>
          {this.props.location.state.context === "create"?
          <Fragment>
          <div style={{padding: '10px 0px'}}>
            <span>Parent Category: </span>
            <span>{this.state.parent?this.state.parent.title:'null'}</span>
            
            {this.state.parent && <span><Button
                onClick={()=>{
                  this.setState({parent:null})
                }}
                style={{
                  backgroundColor:'transparent',
                  padding:'0px 10px',
                  color:"#000"
                }}
              >
                <Icon icon={{default: 'times-circle', material: 'times-circle'}}/>
              </Button>
            </span>}
          </div>
          <div style={{padding: '10px 0px'}}>
            <Button 
              onClick={()=>{
                this.setState({categoryListModal:true})
              }}
              modifier="large--quiet">
              Add Parent Category
            </Button>
          </div>
          </Fragment>:null}
          <div style={{padding: '10px 0px'}}>
              {this.props.location.state.context === "create"?<Mutation 
                mutation={ADD_CATEGORY}
                onCompleted={(data)=>{
                  console.log("Done",data)
                  this.props.history.goBack()
                }}
                refetchQueries={[{query:GET_WALLPAPER_BY_CATEGORY}]}
                >
                {(addCategory,{ loading, data, error}) => {
                  if(loading) return <div>loading ...</div>
                  return (<Button 
                    onClick={()=>{
                      this.onClickAdd(addCategory)
                    }}
                    modifier="material">
                    Add
                  </Button>)
                }}
              </Mutation>
              :
              <Mutation 
                mutation={MODIFY_CATEGORY}
                onCompleted={(data)=>{
                  console.log("Done")
                  this.props.history.goBack()
                }}
                refetchQueries={[{query:GET_WALLPAPER_BY_CATEGORY}]}
                >
                {(modifyCategory,{ loading, data, error}) => {
                  if(loading) return <div>loading ...</div>
                  return (<Button 
                    onClick={()=>{
                      this.onClickAdd(modifyCategory)
                    }}
                    modifier="material">
                    Update
                  </Button>)
                }}
              </Mutation>}
              
            </div>
        </div>
      </Page>
    );
  }
}
CreateCategory.defaultProps = {
  context:'create'
}
export default CreateCategory;