import React, { Component,Fragment } from 'react';
import { 
  Page, 
  Toolbar, 
  Button, 
  BackButton, 
  ToolbarButton, 
  Icon, 
  Card,
  Fab,
  Modal
} from 'react-onsenui';

//local imports
import CardContiner from './../../component/cardcontainer'

// Webpack CSS import
import 'onsenui/css/onsenui.css';
import 'onsenui/css/onsen-css-components.css';

// Apollo imports
import { Query } from 'react-apollo'
import gql from 'graphql-tag'

export const GET_WALLPAPER_BY_CATEGORY = gql`
query{
  categories{
    _id,
    title,
    subTitle,
    priority,
    wallpaper{
      title,
      url,
      _id,
      download,
      premium
    }
  }
}
`



class Home extends Component {

  state = {
    fabList:false
  }


  renderSections = (categories) => {
    return categories.map(category=>{
      return (<CardContiner 
        key={category._id} 
        title={category.title} 
        subTitle={category.subTitle} 
        _id={category._id} 
        priority={category.priority} 
        wallpapers={category.wallpaper}/>)
    })
  }
  render() {
    return (
      <Query query={GET_WALLPAPER_BY_CATEGORY}>
        {({loading,data,error})=>{
          if(loading) return <div>Loading</div>
          if(error) return <div>{error.message}</div>
          return (<Page 
            renderToolbar={() =>
            <Toolbar>
              <div className="left">
                <ToolbarButton>
                  <Icon icon="md-menu" />
                </ToolbarButton>
              </div>
              <div className="center">
                Home
              </div>
              <div className="right">
                <ToolbarButton onClick={()=>{
                  localStorage.removeItem('token')
                  this.props.history.push('/login')
                }}>
                    <Icon icon="sign-out" />
                  </ToolbarButton>
              </div>
            </Toolbar>
            }
    
          > 
            <Modal 
              onClick={()=>{
                this.setState({fabList:false})
              }}
              animation="fade"
              isOpen={this.state.fabList}>
              <div>
              <Button
                style={{
                  color:"#fff"
                }}
                onClick={()=>{
                  this.props.history.push('/createCategory', { context: 'create'})
                }}
                modifier="large--quiet">
                Add Category
              </Button>
              <Button
                style={{
                  color:"#fff"
                }}
                onClick={()=>{
                  this.props.history.push('/create')
                }}
                modifier="large--quiet">
                Add Wallpaper
              </Button>
              </div>
            </Modal>
            <Fab style={{position:'fixed',bottom:20,right:20}} onClick={()=>{
               this.setState({fabList:true})
            }}>
              <Icon icon='fa-plus' size={26} fixedWidth={false} style={{verticalAlign: 'middle'}} />
            </Fab>
            <div>
              {this.renderSections(data.categories)}
            </div>
          </Page>)
        }}
      </Query>
      
    );
  }
}

export default Home;